@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");

// importing sass
@import "../sass/var";
@import "../sass/mixin";

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

html,
body {
  height: 100%;
  width:100%;
}

body{
  position: relative;

}

/* header style css */
.header-wrapper {
  height: 132px;
  width: auto;
  padding: 10px 20px;
  border: 1px solid #ddd;
  background-color: #e5ebb2;
}

.header-wrapper .main-header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px 0px;
  position: relative;
}

.main-header .header-logo-title {
  text-decoration: none;
  color: $brand-color;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.main-header .header-logo-title .header-logo .logo {
  width: 70px;
  height: 70px;
}

.main-header.header-logo-title .header-title {
}

.header-wrapper .main-header .header-navbar {
  margin: auto;
}
.header-wrapper .main-header .header-navbar-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 50px;
}

.header-navbar-list .list-item {
  list-style: none;
  position: relative;
}

.header-navbar-list .list-item a {
  text-decoration: none;
  color: #000;
  padding: 10px 20px;
  font-size: 18px;
}

.header-navbar-list .list-item a:hover,
.header-navbar-list .list-item a:active {
  cursor: pointer;
  color: #ff8787;
}

// .header-navbar-list .list-item a:active::after,
.header-navbar-list .list-item a:hover::after {
  padding: 10px 20px;
  width: 10%;
  content: "";
  position: absolute;
  border-bottom: 2px solid #ff8787;
  left: 20px;
  bottom:0px;
}

