/*................................hero section.................... */
.hero-section{
  width: 100%;
  height:500px;
  background-color:gray;
}

.hero-section .hero-container{
  display:flex;
  justify-content:flex-start;

}


.hero-section .hero-container .hero-image {
  width:100%;
  height: 500px;
  background-image: linear-gradient(rgba(4, 9, 30, 0.3), rgba(4, 9, 30, 2)),
    url("../images/hero.png");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-position: center;
  display: flex;
  justify-content:space-around;
  align-items:center;

}
.hero-section .hero-container .hero-image .btn{
  color:#fff;
}

.hero-section .hero-container .hero-image .btn:hover{
  cursor:pointer;
}

.hero-container  .hero-image .hero-content{
  color: #fff;
  line-height:2rem;
  text-align:center;
}
.hero-container  .hero-image .hero-content h1{
  font-size:2rem;
}
.hero-container  .hero-image .hero-content span{
  color:#ff8787;
}

// .hero-container  .hero-image .hero-content .signup-btn{
//   @include btnBgClr(rgb(255, 135, 135), #fff);
// }
.hero-container  .hero-image .hero-content .signup-btn:hover{
  background:#BCE29E;
  color:#000;
}


/*..............................about us..........................*/
.about-section {
  width: 100%;
  height: 500px;
}

.about-section .about-container{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding: 80px 40px;
  background-color:#ffe7e0;
}


.about-section .about-container .about-left{
  width: 500px;
   height: 300px;
}


.about-section .about-container .about-left .about-content h3 {
  padding: 30px 0px;
  font-size: 30px;
}


.about-section .about-container img{
  width: 400px;
  height: 300px;
}


/*menu */
.menu-section{
  width: 100%;
  height: 600px;
}

.menu-section .menu-container{
  width: 100%;
  padding: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 40px;
  border: 1px solid #ccc;
}

.menu-container .menu-description h4{
  font-size: 25px;
  text-align: center;
  position: relative;
}


.menu-container .menu-content{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 2rem;
  gap: 2rem;
}

.menu-container .menu-content .menu-card .menu-btn {
  @include btnBgClr(rgb(188, 226, 158), #000);
}

/*why choose food */
.choose-food-container {
  width: 100%;
  height: 500px;
}

.choose-food-container .choose-food-image {
  display: flex;
  justify-content: space-evenly;
  padding-top: 40px;
  align-items: center;
  gap: 0px;
}

.choose-food-container .choose-food-image .left-food-image .food-image {
  flex: 400px;
  height: 400px;
  object-fit: cover;
  // box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  filter: drop-shadow(-22px 16px 20px #000);
}

.choose-food-container .choose-food-image .right-food-info {
  width: 700px;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.right-food-info .food-taste {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  padding: 40px;
}

.right-food-info .food-taste .food-icon .icon .MuiSvgIcon-root{
  color:#ff8787;
  font-size:2rem;
  
}



.right-food-info .food-taste .food-title {
  font-size: 20px;
  font-weight: 600;
  padding: 10px;
}




/* hall container*/
.hall-container {
  width: 100%;
  height: 600px;
  background-color: #BCE29E;
}

.hall-container .hall-main-info {
  display: flex;
  justify-content: space-around;
  gap: 100px;
  padding: 100px 20px;
}

.hall-container .hall-main-info .hall-info{
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.hall-container .hall-main-info .hall-info .hall-book-btn .book-btn{
    padding:10px 20px;
    background-color:#ff8787;
    color:#fff;
    border-radius:5px;
    border:1px solid #fff;

}


.hall-container .hall-main-info .hall-info .hall-book-btn .book-btn:hover{
    border:1px solid #fff;
    padding:10px 20px;
    border:1px solid #ff8787;
    background-color:#fff;
    color: #ff8787;
    cursor: pointer;



}

.hall-container .hall-main-info .hall-info h4 {
  font-size: 25px;
}

.hall-container .hall-main-info .hall-image-data {
  display: flex;
}
.hall-container .hall-main-info .hall-image-data .hall-img {
  width: 400px;
  height: 300px;
  transform: translate(100px, 90px);
  border: 4px solid #fff;
}
.hall-container .hall-main-info .hall-image-data .hall-img-pizza {
  width: 400px;
  height: 300px;
}

.hall-container .hall-main-info .hall-info {
}


/* client review */
.client-review{
    width:100%;
    height:600px;
    background-color:#fff;

}

.client-review .client-container{
    display:flex;
    align-items:center;
    flex-direction:column;
    padding:50px 0px;
    gap:30px;

}

.client-review .client-container .client-title h4{
    font-size:25px;
}


.client-review .client-container .client-review-section{
    display:flex;
    justify-content:space-between;

}

.client-review-section .client-section .client-para{
  width:600px;

}

.client-review-section .client-section{
  padding:20px 70px;
}

.client-review-section .client-section .client-details{
    display:flex;
    justify-content:center;
    align-items:center;
    gap:30px;
    padding:20px;
}


.client-review-section .client-section .client-details .client-image img{
  width:70px;
  height:70px;
  border-radius:50%;
}

.client-review-section .client-section .client-details .client-info h5{
      padding:5px 0px;  
}


/*............................gallery............................... */
.gallery{
  width:100%;
  height:600px;
}

.gallery .gallery-container{
    display:flex;
    align-items:center;
    flex-direction:column;
  
}

.gallery .gallery-container .gallery-title h4{
  font-size:1.5rem;
}


.gallery .gallery-container .gallery-images{
  display:grid;
  grid-template-columns:1fr 1fr 1fr;
  grid-column:4px;
  grid-row: 5px;
  padding-top:50px;
  gap:2rem;
}

.gallery .gallery-container .gallery-images img{
  width:400px;
  height:200px;
}


// /* footer section */
.footer{
  width:100%;
  height:400px;

}
.main-footer{
  width:100%;
  height:400px;
  background-color:rgb(26, 26, 26);
  display:flex;
  justify-content:space-between;
  flex-direction:column;
}

.main-footer .footer-top{
  display:flex;
  justify-content:space-around;
  padding-top:2rem;
}

.main-footer .footer-top .footer-title{
  color:#fff;
  letter-spacing:0.1rem;
}

.main-footer .footer-bottom{
  display:flex;
  background-color:rgb(14, 13, 13);
  justify-content:space-around;
  align-items:center;
  color:#fff;
  gap:2rem;
  padding:2rem;

}

.main-footer .footer-bottom .sociallinks{
  display:flex;

}

